.page {
  height: 100%;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  scroll-behavior: smooth;
}

.page__body {
  min-width: 100%;
  margin: 0 auto;
  height: 100%;

