  padding-left: 5px;
  margin: 0 0 0 auto;
  display: flex;
  justify-content: space-between; }
  @media (max-width: 1199px) {
    .page-footer__social {
      width: 17%; } }
  @media (max-width: 767px) {
    .page-footer__social {
      width: 49%;
      margin: 0;
      align-items: center; } }

.page-footer__social li {
  display: flex; }

.page-footer__social a {
  display: flex; }
  .page-footer__social a:hover, .page-footer__social a:focus-visible {
    opacity: 0.8; }

@media (max-width: 1199px) {
  .page-footer__social img {
    margin: 0 5px; } }

@media (max-width: 767px) {
  .page-footer__social img {
    margin: 0; } }

.page-footer__social-item--vk img {
  padding-bottom: 5px; }
  @media (max-width: 1199px) {
    .page-footer__social-item--vk img {
      padding-left: 0; } }
  @media (max-width: 767px) {
    .page-footer__social-item--vk img {
      width: 32px;
      height: 17px;
      margin: 0;
      padding: 0; } }

@media (max-width: 767px) {
  .page-footer__social-item--ig {
    margin: 0 35px; } }

@media (max-width: 767px) {
  .page-footer__social-item--ig img {
    width: 27px;
    height: 27px; } }

@media (max-width: 767px) {
  .page-footer__social-item--fb img {
    width: 14px;
    height: 27px;
    margin: 0; } }

.btn {
  display: inline-flex;
  margin: 10px 0;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease; }
  .btn--red {
    background-color: #ff1553; }
  .btn:disabled {
    opacity: 0.5;
    pointer-events: none; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .btn:hover, .btn:focus {
      opacity: 0.8; } }

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .modal::-webkit-scrollbar {
    display: none; }
  .modal__wrapper {
    position: relative;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px; }
    @media (max-width: 767px) {
      .modal__wrapper {
        padding: 60px 16px; } }
  .modal__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer; }
  .modal__content {
    position: relative;
    width: 600px;
    padding: 40px;
    background-color: #ffffff; }
    @media (max-width: 767px) {
      .modal__content {
        width: 100%;
        padding: 20px; } }
  .modal__close-btn {
    position: absolute;
    top: 22px;
    right: 22px;
    z-index: 1;
    width: 40px;
    height: 40px;
    padding: 0;
    background: rgba(255, 255, 255, 0);
    border: none;
    cursor: pointer; }
  .modal--responsive .modal__content {
    width: 100%; }
  .modal--fit-content .modal__content {
    width: auto; }
    @media (max-width: 767px) {
      .modal--fit-content .modal__content {
        width: 100%; } }
  .modal--no-scale {
    transform: none; }
  .modal--preload {
    transition: none; }
  .modal.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto; }

